.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 5px solid rgb(180, 180, 180);
  /* float: left; */
  /* margin-right: -1px; */
  /* margin-top: -1px; */
  padding: 0;
  text-align: center;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  background-size: cover;
}

.square:focus {
  outline: none;
}

.playerA {
  /* background: red; */
  background-image: url("./tanson.jpg");
  border: 5px solid rgb(82, 140, 247);
}

.playerB {
  /* background: yellow; */
  background-image: url("./sherly.jpg");
  border: 5px solid rgb(255, 147, 165);
}

.winned {
  animation: App-logo-spin infinite 3s linear;
}
